
.scrollingThing {
    overflow-y: hidden;
}
.scrollingThing:hover {
    overflow-y: auto;
}
.scrollX {
    overflow-x:hidden;
}
.scrollX:hover {
    overflow: auto;
}
button {
    background-color: transparent;
    border-width: 0px;
}
input {
    margin-top: 20px;
    box-sizing: border-box;
    border: 2px solid rgb(235,235,235);
    outline: none; 
    padding: 12px 20px;
    height: 50px;
    border-radius: 10px;
    padding-bottom: 20px;
    width: 200px
}
.navigation {
    position: fixed;
}
.todo-div {
    display: inline-block;
    width: 200px;
    
    margin-left: 100px;
    /*float: left; */
}
.login-button {
    border: 1cm;
    background-color: #8b4389;
    border-radius: 5px;
}

.video-outer-div {
    position: absolute;
    bottom: -99vh;
    z-index: -3;
    -webkit-overflow-scrolling: touch;
    display: inline-block; 
    
    
    margin-left: calc(60px + 20vw);
    overflow-y: auto;
    height: auto;
}
input:focus { 
    border: 1px solid #0af; 
    outline: none; 
    -moz-box-shadow: 0px 0px 15px rgba(0, 170, 255,.5);
    -webkit-box-shadow: 0px 0px 15px rgba(0, 170, 255,.5);
    box-shadow: 0px 0px 15px rgba(0, 170, 255,.5); }

.todo-outer-div {
    margin-left: 300px;
    margin-top: 100px;
    display: inline-block; 
    overflow-X: auto; 
    position: relative; 
    height: calc(100vh - 105px);
    width: calc(100vw - 300px);

}
.dropdown-menu-item:hover {
    background-color: #e8e8e8;
    
}

.teacher-overview-outer-div {
    background-color: #F8F8F8; 
    height: 100%; 
    width: 100%; 
    border-radius: 3vh; 
    overflow: hidden;
}

.teacher-overview-outer-div:hover {
    overflow: auto
}

.teacher-overview-item {
    position: relative;
    padding: 2%; 
    height: 8.5vh;  
    align-items: center; 
    font-size: 100%; 
    display: flex; 
    border-bottom: 2px solid #bbb;
    cursor: pointer
}

.teacher-overview-item:hover {
    background-color: #e8e8e8;
}

.popup-background-div {
    position: fixed; 
    bottom: 0; 
    left: 0; 
    height: 100vh; 
    width: 100vw; 
    justify-content: center; 
    align-items: center; 
    display: flex;
}
.add-button-text {
    transform: translatey(-2px); 
    cursor: pointer; 
    font-size: 15px; 
    color: white;
}
.add-task-button {
    cursor: pointer; 
    background-color: #8b4389; 
    border-radius: 7px; 
    height: 30px;
}
.cancel-button {
    margin-left: 10px; 
    cursor: pointer; 
    background-color: #c8c8c8; 
    border-radius: 7px; 
    height: 30px; 
    width: 80px;
}

.popup-clickable-background-div {
    position: fixed; 
    bottom: 0; 
    left: 0; 
    height: 100vh; 
    width: 100vw; 
    background-color: black; 
    opacity: 0.5;
}
div {
    font-family: /*'Segoe UI',  */ 'Open Sans', Tahoma, Geneva, Verdana,sans-serif; 
    /*font-family: "Comic Sans MS", "Comic Sans", cursive;*/
}
@font-face {
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 400;
    src: local("Open Sans"), local("OpenSans"), url(https://themes.googleusercontent.com/static/fonts/opensans/v6/K88pR3goAWT7BTt32Z01mz8E0i7KZn-EPnyo3HZu7kw.woff) format('woff');
  }
body {
    overflow: hidden;
    font-family: 'Segoe UI','Open Sans', Tahoma, Geneva, Verdana, sans-serif; 
    -webkit-user-select: none; /* Safari */        
-moz-user-select: none; /* Firefox */
-ms-user-select: none; /* IE10+/Edge */
user-select: none; /* Standard */
    /*-webkit-overflow-scrolling: touch; */
}

.perks-rewards-scrollable {
    overflow-x: hidden;
    overflow-y: hidden;
    width: 100%;
    white-space:nowrap;
    height: 100%;
    max-height: 100%;
}
.perks-rewards-scrollable:hover {
    overflow-x: auto;
    white-space:nowrap;
    width: 100%;
    height: 100%
}

.hidden-button {
    opacity: 0;
}
.hidden-button:hover {
    opacity: 1;
}

.journey-checklist {
    display: inline-block;
    background-color: #f8f8f8; 
    border-radius: 10px; 
    font-weight: 500; 
    cursor: pointer;
    width: 130px;
    height: 40px;
    overflow: hidden;
    transition: all 0.5s;
}

.journey-checklist-open {
    display: inline-block;
    background-color: #f8f8f8; 
    overflow: hidden;
    border-radius: 10px; 
    font-weight: 500; 
    cursor: pointer;
    height: 350px;
    width: 300px;
    transition: all 0.5s;
}

.checkpoint-portfolio-download-closed {
    position: relative;
    height: calc(8.5vh + 2%);  
    transition: all 1s;
    overflow: hidden;
}

.checkpoint-portfolio-download-closed:hover {
    background-color: #e8e8e8;
}

.checkpoint-portfolio-download {
    position: relative;
    height: 38.5vh;  
    transition: all 0.5s;
    overflow-x: auto;
    border-bottom: 2px solid #bbb;
    }
    
.checkpoint-portfolio-download:hover {
        background-color: #e8e8e8;
    }

.video-question-alternative {
    width: 280px; 
    border: 3px solid lightgrey; 
    display: inline-block; 
    margin-right: 20px; 
    height: 80px;
    border-radius: 15px; 
    font-size: 20px;
    cursor: pointer;  
}

.video-question-alternative:hover {
    background-color: lightgray;
}

.darken {
    background-color: white;
}

.darken:hover {
    background-color: #e8e8e8;
}





