.not-extended {
    margin-left: -300px;
    transition: margin-left 0.3s;
}
.extended {
    margin-left: 0;
    transition: margin-left 0.3s;
}
.badge-div {
    float: right;
    margin-right: 30px;
    height: 100%;
}
.badge-text {
    font-size: 25px;
    color: white;
    font-weight: normal;
    display: inline-block;
    transform: translatey(-12px); 
}
.navigation {
    position: fixed;
}
.navbar-base {
    background-color: #8B4389;
    width: 100vw;
    height: 50px;
    max-height: 15vh;
  
}
.navbar-internal-div {
    transform: translatey(4px);
}
.navbar-text {
    font-size: 25px;
    color: white;
    font-weight: normal;
    display: inline-block; 
    margin-left: 20px;
    transform: translatey(-12px);
}

.sidenav {
    background-color: #F8F8F8;
    height: 100vh; 
    width: 300px;
    max-width: 25vw;
    min-width: 10vw;
}

.sidenav-text {
    text-decoration: none;
    font-size: 2vh;
    font-weight: normal;
    color: black; 
    height: 7vh;
    
}
.sidenav-internal-div {
    margin-left: 50px;
    transform: translatey(80px);
}